<!-- 收运总列表 -->
<template>
	<view class="container">
		<!-- 顶部搜索栏 -->
		<view class="search-bar">
			<uni-icons class="search-icon" type="search" size="16" color="#999"></uni-icons>
			<input class="search-input" type="text" placeholder="按单号/物品关键词" placeholder-class="placeholder" />
		</view>
		<!-- 筛选条件栏 -->
		<view class="filter-bar">
			<view class="filter-item">
				<text>状态</text>
				<uni-icons type="bottom" size="12" color="#666"></uni-icons>
			</view>
			<view class="filter-item">
				<text>时间</text>
				<uni-icons type="bottom" size="12" color="#666"></uni-icons>
			</view>
		</view>
		<!-- 订单列表 -->
		<view class="content-wrapper">
			<scroll-view class="content" scroll-y>
				<view class="order-list">
					<view class="order-item" v-for="(item, index) in orderList" :key="index">
						<view class="order-header">
							<view class="shop-info">
								<text class="shop-name">{{ item.shopName }}</text>
								<text :class="['status-tag', item.status === '预约中' ? 'booking' : (item.status === '进行中' ? 'processing' : 'completed')]">
									{{ item.status }}
								</text>
							</view>
						</view>
						<view class="order-content">
							<view class="info-item">
								<text class="label">今日收运：</text>
								<text class="value">{{ item.deliveryCount }}桶</text>
							</view>
							<view class="info-item">
								<text class="label">收运重量：</text>
								<text class="value">{{ item.weight }}</text>
							</view>
							<view class="info-item">
								<text class="label">车辆信息：</text>
								<text class="value">{{ item.carInfo }}</text>
							</view>
							<view class="info-item">
								<text class="label">收运时间：</text>
								<text class="value">{{ item.time }}</text>
							</view>
						</view>
						<view class="order-footer">
							<uni-button size="mini" class="btn-cancel" v-if="item.status !== '已完成'">取消</uni-button>
							<uni-button size="mini" :type="item.status === '预约中' ? 'primary' : 'default'" class="btn-confirm">
								{{ item.status === '预约中' ? '确认收运' : (item.status === '进行中' ? '完成收运' : '查看详情') }}
							</uni-button>
						</view>
					</view>
				</view>
				<view class="loadMore">
					<uni-load-more :status="loadingStatus" :content-text="{
						contentdown: '上拉显示更多',
						contentrefresh: '正在加载...',
						contentnomore: '没有更多数据了'
					}"></uni-load-more>
				</view>
			</scroll-view>
		</view>
	</view>
</template>
<script lang="ts" setup>
	import { ref } from 'vue';
	
	// 订单列表数据
	const orderList = ref([
		{
			shopName: '川味小厨（总店）',
			status: '预约中',
			deliveryCount: 5,
			weight: '4.24KG',
			carInfo: '川A3D47M',
			time: '2025-08-20 14:05:30'
		},
		{
			shopName: '湘味私房菜',
			status: '进行中',
			deliveryCount: 3,
			weight: '2.5KG',
			carInfo: '沪B12345',
			time: '2025-08-20 15:30:00'
		},
		{
			shopName: '粤式烧腊',
			status: '已完成',
			deliveryCount: 4,
			weight: '3.8KG',
			carInfo: '京C67890',
			time: '2025-08-20 16:45:00'
		}
	]);
	
	// 加载状态
	const loadingStatus = ref('more');
</script>
<style lang="scss" scoped>
	page {
		height: 100%;
		background-color: #f5f5f5;
	}
	
	.container {
		height: 100%;
		display: flex;
		flex-direction: column;
		
		.search-bar {
			padding: 20rpx 30rpx;
			background-color: #ffffff;
			display: flex;
			align-items: center;
			position: relative;
			
			.search-icon {
				position: absolute;
				left: 50rpx;
				z-index: 1;
			}
			
			.search-input {
				flex: 1;
				height: 72rpx;
				background-color: #f5f5f5;
				border-radius: 36rpx;
				padding: 0 80rpx;
				font-size: 14px;
			}
		}
		
		.placeholder {
			color: #999999;
		}
		
		.filter-bar {
			display: flex;
			padding: 20rpx 30rpx;
			background-color: #ffffff;
			border-bottom: 1px solid #f0f0f0;
			justify-content: center;
			
			.filter-item {
				display: flex;
				align-items: center;
				margin: 0 40rpx;
				
				text {
					font-size: 14px;
					color: #333333;
					margin-right: 8rpx;
				}
			}
		}
		
		.content-wrapper {
			flex: 1;
			overflow: hidden;
			
			.content {
				height: 100%;
				overflow: auto;
				padding: 20rpx 30rpx;
				
				.order-list {
					.order-item {
						background-color: #ffffff;
						border-radius: 12rpx;
						padding: 30rpx;
						margin-bottom: 20rpx;
						
						.order-header {
							display: flex;
							justify-content: space-between;
							align-items: center;
							margin-bottom: 20rpx;
							
							.shop-info {
								display: flex;
								align-items: center;
								
								.shop-name {
									font-size: 16px;
									color: #333333;
									font-weight: 500;
									margin-right: 20rpx;
								}
								
								.status-tag {
									font-size: 12px;
									padding: 4rpx 12rpx;
									border-radius: 40rpx;
									
									&.booking {
										background-color: #e6f7ff;
										color: #1890ff;
									}
									
									&.processing {
										background-color: #fffbe6;
										color: #faad14;
									}
									
									&.completed {
										background-color: #f0fff0;
										color: #52c41a;
									}
								}
							}
						}
						
						.order-content {
							.info-item {
								display: flex;
								margin-bottom: 16rpx;
								
								.label {
									font-size: 14px;
									color: #666666;
									width: 140rpx;
									flex-shrink: 0;
								}
								
								.value {
									font-size: 14px;
									color: #333333;
									flex: 1;
								}
							}
						}
						
						.order-footer {
							margin-top: 20rpx;
							display: flex;
							justify-content: flex-end;
							
							.btn-cancel {
								margin-right: 20rpx;
								background-color: #ffffff;
								border: 1px solid #cccccc;
								color: #333333;
							}
							
							.btn-confirm {
								background-color: #ffffff !important;
								border: 1px solid #52c41a !important;
								color: #52c41a !important;
							}
						}
					}
				}
				
				.loadMore {
					padding: 20rpx 0;
				}
			}
		}
	}
</style>